<template lang="pug">

  .contacts.calls-fixed-height
    .header
      .nav
        router-link(:to="{ name: 'CreateContact', params: { options: {type: 'create', data: {number: ''}}} }")
          svg.nav__option.nav__option_icon
            use(xlink:href='#Add')

      h1.header__title Контакты

      .header-search
        svg.header-search__icon
          use(xlink:href='#Search')
        input(
          type='text'
          placeholder='Поиск'
          v-model.trim='search').header-search__input

    .contacts-list
      p.contacts-list__phone-number Мой номер: {{phoneNumber}}

      ContactItem(:from='"contacts"' :search='search')

</template>

<script>

  import {mapState} from 'vuex'
  import ContactItem from './ContactItem'
  
  export default {
    components: {
      ContactItem
    },
    data() {
      return {
        search: '',
      }
    },
    computed: mapState({
      phoneNumber: state => state.player.phoneNumber
    })
  }

</script>

<style lang="sass">

  @import '../../assets/sass/phone/apps/calls/contacts'

</style>